/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>写《革命后社会的政府》，集中加工社会变革一章。这一章分析在改革开放推进之后，社会发生了什么样的变革，这些变革对政府体制的改革提出什么样的挑战。把原来提出的社会领域的六大变革的政府行政的十大变革充实了一下。同时提出这两组变量之间存在着落差，这一落差是考察政府体制改革的出发点：</p>\n<p>第一，主动和被动的落差。第一组变量的发展速度快，铺展面也广，而且是变动主动的方面。第二组变量发展速度慢，铺展的面也相对窄，是变动的被动的方面。这是符合一般社会的发展的规律的。社会发展总是最生动最有活力的一面，尤其是在社会的各种因素被充分发动起来之后。而政府体制的发展，要人们对社会发展积累的力量的认识达到一定的限度之后，才能被推动并发展起来。</p>\n<p>第二，时间的落差。从时间是来说，客观上存在这样的落差。社会的发展过程，特别是经济发展过程，总领先于社会其他发展。在市场经济体制逐步建立起来之后，这一特征还会更加明显地显示出来。政府体制的改革要在经济力量达到一定的程度之后才能推进，或者说经济发展提出要求后，政府体制才能具备发展的动力。这个时间的落差有时不是主观可以消除的，问题要认识这个落差，尽量来减少落差。在经济快速发展的社会中，这种落差更容易形成。</p>\n<p>第三，体认的落差。社会发展究竟向政府体制提出了什么要求，政府体制究竟应该如何适应社会发展，有体认的问题。对社会发展的客观要求，并不是所有人均能够体认的。就是能够体认，也会产生不同结果，有时甚至是激烈对立的结果。政府体制是敏感地域，对它的改革总涉及全局的变革，所以人在这个问题上往往会有更多的体认分歧。社会发展了，但是在人们的体认没有达到一定的一致之前，要推进政府体制改革不容易。越是发展快的社会，在体认的共同点的形成上可能越困难，原因是社会发展的多向性和多可能性，给人们提供了太多的体认的要素。越是发展变量多，体认越不易。</p>\n<p>第四，设计的落差。政府体制改革需要较为完整的设计，不可以一边变一边设计，政府体制的逻辑是这样。因为一旦政府体制在变化中，那么它的调控功能就会受到影响。所以，政府体制改革需要完整的设计，而完整的设计需要对变化的要素有足够把握。在变化迅速的社会中，不同要素每时每刻均在变化，要在一个时期里达到一定的把握程度，不容易。没有这样把握自然就不能做出合理设计。另一方面，对于中国形成中的新体制，究竟要采取什么样的体制模式，并没有现存模式。中国的历史社会环境、中国的社会结构和社会关系、中国特色的社会主义市场经济体制需要什么样的政府体制与之相适应，不是一个很好回答的问题。总体的设计本身就在探索之中，因而就会有这样的落差。</p>\n<p>第五，操作的落差。最大的问题还在于操作的落差，政府体制可以说是社会调控的中枢神经，至关重要。对它的改革要依整个社会利益为依据，以整个社会能够稳定发展为依据。如果政府体制改革引起社会不稳定或者动乱，成本就太高。不仅政府体制改革不能完成，经济发展和现代化进程也可能会被打断。中国是发展中的国家，是\"超大社会\"，又处于急剧变动中，政府体制承担着十分关键的功能：保持社会的稳定和秩序。一方面是这一功能不能松弛，另一方面是要变革承担这一功能的体制，这就是操作上落差。经济体制改革的操作性要强得多，虽然一些关系国计民生的重要变革操作上也是高难度的，但是政府体制改革总体上都是高难度的。</p>\n<p>因此，从政府体制发展的基本任务来说，就是如何来缩小这两组变量之间的落差。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0100.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0100.md","frontmatter":{"title":"5月29日 星期日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0100.md absPath of file >>> MarkdownRemark"}}

/***/ })

